.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

canvas {
  max-height: 460px !important;
  width: 100%;
  height: auto;
}

h2 {
  margin-top: 0;
}

.max-traffic-title {
  margin-bottom: 4px !important;
}

.max-traffic-text {
  margin-top: 0px !important;
}

.main-container {
  max-width: 1000px;
  margin: 0 auto;
}

.average-tfc-text {
  text-align: left;
}

.banner-image {
  width: 100%;
  max-width: 1000px;
  border-radius: 8px;
}